.skills-section {
  padding: 50px 20px;
  text-align: center;
  background-color: #f8fbff;
}

.skills-section h2 {
  font-size: 2.5rem;
  color: #003366;
  margin-bottom: 10px;
}

.skills-section p {
  font-size: 1rem;
  color: #555;
  margin-bottom: 40px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.skills-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
}

.skill-card {
  background: white;
  border-radius: 12px;
  padding: 20px;
  width: 280px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05);
  text-align: left;
}

.skill-card h3 {
  font-size: 1.2rem;
  color: #003366;
  margin-bottom: 15px;
}

.skill-card i {
  margin-right: 8px;
}

.skill-card ul {
  list-style: none;
  padding: 0;
}

.skill-card li {
  margin-bottom: 12px;
}

.bar-label {
  font-size: 0.9rem;
  margin-bottom: 4px;
  color: #333;
}

.bar {
  width: 100%;
  background: #e0e0e0;
  border-radius: 8px;
  height: 8px;
  overflow: hidden;
}

.bar-fill {
  width: 70%;
  background: linear-gradient(90deg, #2b74ff, #00c4cc);
  height: 100%;
  border-radius: 8px;
}
