.hero-section {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  background: linear-gradient(to bottom right, #1e3c72, #2a5298); /* Blue gradient */
  color: white;
  text-align: center;
  padding: 2rem;
}

.hero-container {
  max-width: 800px;
}

.hero-section h1 {
  font-size: 3.5rem;
  margin: 0.5rem 0;
}

.hero-section h3 {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
}

.hero-section h2 {
  font-size: 1.8rem;
  font-weight: 300;
  color: #c2e7ff;
}

.name-highlight {
  color: #71f7ff;
}

.hero-section p {
  font-size: 1rem;
  margin: 1.5rem 0;
  color: #d4e9f6;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1.5rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: bold;
  transition: background 0.3s ease;
}

.btn-primary {
  background-color: #00f1a1;
  color: black;
}

.btn-outline {
  border: 2px solid #00f1a1;
  color: white;
}

.scroll-down {
  margin-top: 2rem;
  font-size: 2rem;
  animation: bounce 2s infinite;
  color: #ffffffcc;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(10px); }
}


.btn-outline:hover {
  background-color: #00f1a1;
  color: black;
}

/* Use Poppins font globally */
body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  background: #f9f9f9;
  color: #333;
}

/* Navbar styling */
.navbar {
  background-color: #202124;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 60px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.navbar h1 {
  font-size: 1.8rem;
  font-weight: 700;
  margin: 0;
  letter-spacing: 0.5px;
}

/* Navbar links */
.navbar nav a {
  color: white;
  text-decoration: none;
  margin-left: 25px;
  font-weight: 600;
  font-size: 1rem;
  transition: color 0.3s;
}

.navbar nav a:hover {
  color: #3ea6ff;
}


.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 60px 10%;
  background: white;
}

.hero-text {
  max-width: 60%;
}

.hero-image img {
  width: 200px;
  border-radius: 50%;
}

.about-edu {
  padding: 4rem 2rem;
  background: #f9f9f9;
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: space-between;
}

.about-left, .about-right {
  flex: 1 1 45%;
}

.about-left p,
.about-right p {
  font-size: 1rem;
  line-height: 1.6;
  margin: 0.5rem 0;
}

.about-icons p {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin: 0.3rem 0;
}

.education-card, .why-card {
  background: white;
  border-left: 4px solid #356aff;
  border-radius: 6px;
  padding: 1rem;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  margin-bottom: 1rem;
}

.section {
  padding: 40px 10%;
  background: #fff;
  margin: 20px 0;
}

.btn {
  display: inline-block;
  margin-top: 15px;
  padding: 10px 20px;
  background: #0078D4;
  color: white;
  border-radius: 5px;
  text-decoration: none;
}
.project-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  margin-top: 30px;
}

.project-card {
  background-color: #1e1e1e;
  color: #fff;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
  padding-bottom: 15px;
}

.project-card:hover {
  transform: scale(1.03);
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-bottom: 1px solid #333;
}

.project-card h3 {
  margin: 15px 20px 5px;
  font-size: 1.2rem;
}

.project-card p {
  margin: 5px 20px 15px;
  font-size: 0.95rem;
  color: #ddd;
}

.project-card a {
  margin-left: 20px;
  font-weight: 600;
  color: #3ea6ff;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
}

.project-card a i {
  margin-right: 6px;
}

.contact-section {
  padding: 60px 10%;
  background-color: #f8f8f8;
  text-align: center;
}

.contact-section h2 {
  font-size: 2rem;
  margin-bottom: 30px;
  color: #222;
}

.contact-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  justify-content: center;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1rem;
}

.contact-item i {
  font-size: 1.2rem;
  color: #0078d4;
}

.contact-item a {
  text-decoration: none;
}

.link-label {
  font-weight: bold;
  color: #000;
}
.publications-section {
  background-color: #f9f9f9;
  padding: 50px 10%;
  border-top: 1px solid #eee;
}

.publication-card {
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;
  padding: 20px;
  background: #ffffff;
  border-left: 5px solid #0078D4;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}

.publication-card i {
  font-size: 2rem;
  color: #0078D4;
  margin-right: 20px;
}

.publication-card h3 {
  margin: 0 0 8px;
  font-size: 1.2rem;
  font-weight: 700;
}

.publication-card p {
  margin: 0 0 10px;
  color: #555;
  line-height: 1.5;
}

.publication-card a {
  color: #0078D4;
  font-weight: bold;
  text-decoration: none;
}

.publication-card a:hover {
  text-decoration: underline;
}

.paper-description {
  display: inline;
  margin-left: 10px;
  font-size: 0.9rem;
  color: #444;
}

.publications-section .contact-item {
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
}
